<template>
    <div class="mine_info_layer">
        <header class="mine-header-bar clearfix">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title">个人资料</h2>
            <a  class="topbar-left" @click="submitUserInfos($event)">保存</a>
        </header>
        <main class="mine-u-info" id="mine-data-layout">
            <section class="user-info-sec">
                <div class="u-name-box touxiang-box">
                   <span class="touxiang-text">头像</span>
                </div>
                <div class="u-photo-box" @click="goAvatarPage()">
                    <img :src="images" alt="" class="u-photo" :class="{'u-photo-no':!isTouXiang}">
                    <i class="touxiang-arrow-right"></i>
                </div>
            </section>
            <section class="user-info-sectwo">
              <div class="u-name-box sex-box">
                <!--<i class="u-icon"></i>-->
                <span class="sex-title">名字</span>
                <!--<img class="u-icon" src="/static/images/u-iicon@3x.png">-->
                <input class="sex-setting" v-model="personalInfo.name" placeholder="点击设置">
              </div>
                <div class="sex-box">
                    <!--<i class="sex-icon"></i>-->
                    <span class="sex-title">性别</span>
                    <input  class="sex-setting" v-model="personalInfo.sex" placeholder="点击设置">
                </div>
                <div class="sex-box">
                    <!--<i class="sex-icon party-icon"></i>-->
                    <span class="sex-title">生日</span>
                    <a  class="sex-setting"  @click="open($event)">{{personalInfo.birthday}}</a>
                </div>
                <div class="sex-box sing-box">
                    <!--<i class="sex-icon sing-icon"></i>-->
                    <span class="sex-title">签名</span>
                    <input type="text" class="sex-setting"  v-model="personalInfo.signture" placeholder="点击设置">
                </div>
            </section>
            <section class="user-info-sectwo">
                <div class="sex-box">
                    <!--<i class="sex-icon company-icon"></i>-->
                    <span class="sex-title">公司</span>
                    <input type="text" class="sex-setting" v-model="companys" placeholder="点击设置">
                </div>
                <div class="sex-box" style="display: none">
                    <!--<i class="sex-icon phone-icon"></i>-->
                    <span class="sex-title">手机</span>
                    <input type="tel" class="sex-setting" placeholder="点击设置">
                </div>
                <div class="sex-box">
                    <!--<i class="sex-icon qq-icon"></i>-->
                    <span class="sex-title">qq</span>
                    <input type="number"  class="sex-setting qq-setting" v-model="userDatas.qq" placeholder="点击设置">
                </div>
                <div class="sex-box sing-box">
                    <!--<i class="sex-icon wechat-icon"></i>-->
                    <span class="sex-title">微信</span>
                    <input type="text"  class="sex-setting"  v-model="personalInfo.wechat_number" placeholder="点击设置">
                </div>
            </section>
        </main>
      <div class="remember">
        <mt-datetime-picker
          ref="picker"
          type="date"
          v-model="pickerValue"
          :startDate="startDate"
          :endDate="endDate"
          @confirm="handleConfirm"
        >
        </mt-datetime-picker>
      </div>
      <section class="personal-info-item3" v-show="!isShowLogin">
        <a @click="logout($event)" class="btn-logout">退出当前账号</a>
      </section>
    </div>
</template>
<script>
    import { DatetimePicker } from 'mint-ui';
    import  timeFormat from '../../../utils/times';
    import Api from '../../../api/api';
    import {getUser} from '../../../api/userinfo';
    import {changeUserInfo} from '../../../api/userinfo';
    import { Toast } from 'mint-ui';
    import CheckUtils from '../../../utils/check';
    import logUtil from '../../../utils/log';
    import {mapGetters,mapActions,mapState} from 'vuex';
    export default{
        computed:mapGetters(['isShowLogin']),
        data(){
          return{
            //设置当前时间
            pickerValue:new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate()),
            //自定义选择开始-结束时间
            startDate:new Date(new Date().getFullYear() - 60, 0, 1),
            endDate:new Date(new Date().getFullYear(), 11, 31),
            userDatas:{},
            //个人信息
            personalInfo:{},
            //用户头像信息
            userIcon:{},
            //公司信息
            companys:'',
            isTouXiang:false,
            images:""
          }
        },
        mounted(){
          this.getUserInfos();
          this.showH();
          this.userIcon.medium = localStorage.getItem('user-avatar');
          if(this.userIcon.medium){
              this.isTouXiang = true;
          }else{
            this.isTouXiang = false;
          }
        },
        methods:{
            showH(){
              this.isShowLogin = localStorage.getItem('login');
              /*console.log("this.isShowLogin:",this.isShowLogin);*/
              if(this.isShowLogin === false){
                localStorage.setItem('login',false);
                this.$store.dispatch('showLogin',false);
              }else{
                localStorage.setItem('login',true);
                this.$store.dispatch('showLogin',true);
              }
            },
          /**
           * 退出登录
           * @param $event
           */
          logout($event){
            //清除保存的信息
            let token = window.localStorage.getItem('token');
            this.$toast({
              message: "成功退出",
              duration: 2000,
            });
            localStorage.removeItem('uPhoto');
            localStorage.removeItem('uName');
            localStorage.setItem('login',true);
            this.$store.dispatch('showLogin',true);
            this.$store.dispatch('clearToken');
            this.$store.dispatch('clearUserId');
            this.$router.replace('/index');
            $event.preventDefault();
          },
         /**
          * 获取用户信息
          */
         getUserInfos(){
           const _this = this;
           getUser(Api.USERINFOS).then((res)=>{
              _this.initUserInfos(res);
           },(err)=>{
              _this.hideUserInfo();
           });
         },
          /**
           * 初始化用户的信息
            * @param res
           */
         initUserInfos(res){
            const _this = this;
             if(res.status === 200){
               _this.userDatas = res.data[0];
               _this.personalInfo = res.data[0].personal_info;
               _this.images = _this.personalInfo.image.medium;
               console.log("_this.images:",_this.images);
               if(_this.personalInfo.avatar){
                 _this.isTouXiang = true;
               }else{
                 _this.isTouXiang = false;
               }
              /* if(res){

               }*/
               logUtil.printLog("获取到的用户信息是:",res);
               if(res.data[0].company!==null){
                 _this.companys = res.data[0].company.name;
               }

               /**
                * _this.userIcon对象三个属性:small,medium,large
                */
               _this.userIcon = this.personalInfo.image;
               localStorage.setItem('uName',this.personalInfo.name);
               localStorage.setItem('uPhoto',this.personalInfo.avatar);
               localStorage.setItem('uSign',this.personalInfo.signture);
               if(_this.personalInfo.birthday === '' || _this.personalInfo.birthday===null){
                 _this.personalInfo.birthday = '点击设置';
               }
               if(_this.personalInfo.sex === 'm'){
                 _this.personalInfo.sex = '男';
               }else{
                 _this.personalInfo.sex = '女';
               }
             }else{
               _this.hideUserInfo();
             }
         },

         hideUserInfo(){
           this.showMsg('获取用户信息失败，请重试');
           this.$store.dispatch('hideLoading');
         },
         showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
         },
          /**
           * 跳转到设置头像的界面
            */
          goAvatarPage(){
            //设置去到设置头像的界面的时候预览的用户头像
            let icon = this.personalInfo.avatar;
            localStorage.setItem('avatar',icon);
            this.$router.push({path: `/setavatar`, replace: true});
            /*this.$router.push({path: `/pdf`, replace: true});*/
          },
          /**
           * 将时间选择控件弹出
           * */
          open($event) {
            this.$refs.picker.open();
            $event.preventDefault();
          },
          /**
           * 设置用户的生日为dateTimePiacker当前选中的值
           * */
          handleConfirm(){
            this.personalInfo.birthday = timeFormat(this.pickerValue,false);
          },
          /**
           * 提交用户修改的信息
           * @param $event
           */
          submitUserInfos($event){
              const _this = this;
              /*if(!CheckUtils.isQQ(_this.userDatas.qq)){
                  _this.showMsg('请输入正确的QQ号');
                  return;
              }*/
              //用户头像
              let personal_info={
                name:_this.personalInfo.name,
                sex:_this.personalInfo.sex === '男'?'m':'f',
                birthday:_this.personalInfo.birthday!=='点击设置'?this.personalInfo.birthday:null,
                signture:_this.personalInfo.signture,
                wechat_number:_this.personalInfo.wechat_number,
                avatar:localStorage.getItem('user-avatar')
//                avatar:'hello test'
              };
              //公司信息
              let company = {
                name:_this.companys
                  /*name:{
                    name:_this.companys
                  },*/
              };
              changeUserInfo(Api.USERPLOADSINFOS,_this.userDatas.qq,personal_info,company).then((res)=>{
                  _this.showUploadSuccessInfo(res.status);
               },(err)=>{
                  _this.showUploadFailInfo();
               });
              $event.preventDefault();
          },
          /**
           *  用户信息上传成功的提示
           * @param status:状态码
           */
          showUploadSuccessInfo(status){
            if(status === 201){
              this.showMsg('恭喜您,用户信息上传成功!');
              this.$router.go(0);//refresh current page
            }else{
              this.showUploadFailInfo();
            }
          },
          /**
           * 用户信息上传失败的提示
           */
          showUploadFailInfo(){

            this.$store.dispatch('hideLoading');
            this.showMsg('上传用户信息失败，请重试!');
          },
        },
    }
</script>
<style scoped>
    @import "../../../assets/css/mineinfo.css";
    body {
        background-color: #EDEEF0;
    }
</style>



